// header漢堡選單
$('.ham-icon').click(function (e) {
  e.preventDefault();
  $('.mobileMode').toggleClass('show')
})
// 產品頁切換
$('.product_menu li').click(function (e) {
  e.preventDefault();
  var name = $(this).attr('href');
  var unused = $(this).hasClass('unused');
  if (unused) {
    return
  } else {
    // 菜單底線切換
    $(this).addClass('active');
    $(this).siblings().removeClass('active')
    //產品頁切換  
    $('.product_intro').removeClass('active');
    $(name).addClass('active')
  }
})
